#project-section {
    padding:0;
    padding-left: 5%;
    padding-right: 5%;

    .project-div{
        
        .description {
            padding-left:10%;
            padding-right:10%;
            
            .title {
                font-size: 2vmin;
                color: rgb(45, 45, 45);
                font-weight: 600;
                text-align:center;

            }
            .content {
                font-size: 1.5vmin;
                color: rgb(45, 45, 45);
                text-align:left;
            }

            .list {
                font-size: 1.5vmin;
                color: rgb(45, 45, 45);
                padding: 1%;
            }

        }
    }


    
    @media only screen and (max-width: 769px) {
        .mantine-Carousel-root {
            padding-left: 0px;
            padding-right: 0px;
        }
    }
}