#news-section {
    padding-left:15%;
    padding-right:15%;
    padding-top:2%;
    background-color: #1C7ED6;

    .featured-container {
        padding-top: 5%;
        padding-bottom: 2%;

        .title {
            padding-bottom:2%;
            font-size: 3vmin;
            color: white;
            font-weight: 700;
        }
        
        .featured-news-card {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .news-card-image {
                order: 1;
            }
            .news-card-contents {




                order: 2;
                padding: 3%;

                .featured-title {
                    font-size:2vmin;
                    font-weight:600;
                    padding-bottom: 1%;
                }
                
                .accordion {
                    padding-top: 1%;
                    padding-bottom: 2%;
                    font-size:1.2vmin;

                    .mantine-Accordion-label {
                        font-size:1.2vmin;
                    }

                }

                .content {
                    font-size:1.2vmin;

                }
            }        
        }
    }

    .news-container {

        .main-news-card {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .news-card-image {
                order: 1;
            }
            .news-card-contents {
                order: 2;
                margin-bottom:5%;

                .title {
                    font-size: 1.5vmin;
                    font-weight: 500;
                }
                .excerpt {
                    font-size:1.2vmin;
                }
            }
            .news-card-button {
                order: 3;
                margin-top:auto;
            }
        }
    }

}
